export const getTitle = (title: string) => {
  const titles = [
    ["unknown", ""],
    ["pending", ""],
    ["peercard", ""],
    ["roundwolf", ""],
    ["seer", ""],
    ["witch", ""],
    ["daybreaks", ""],
    ["deadskill", ""],
    ["startopinion", ""],
    ["opinion 1", "1"],
    ["opinion 2", "2"],
    ["opinion 3", "3"],
    ["opinion 4", "4"],
    ["opinion 5", "5"],
    ["opinion 6", "6"],
    ["opinion 7", "7"],
    ["opinion 8", "8"],
    ["opinion 9", "9"],
    ["opinion 10", "10"],
    ["publicvote", ""],
    ["publicvoteresult", ""],
    ["gameover", ""],
    ["", ""],
    [undefined, ""]
  ];
  const result = titles.find(x => x[0] === title);
  return (result && result[1]!) || "";
};
